* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

ul, li, h1, p, button, section, div {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hidden {
  display: none;
}

.container__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 400;
}

.container__result {
  font-weight: 700;
}

.container__input {
  width: 160px;
  height: 40px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #454545;
  outline: none;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.container__input:focus {
  outline: none;
}

.container__button {
  width: 160px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #454545;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  transition: all .3s;
  display: block;
}

.container__button:hover {
  background-color: #e3e3e3;
}

