.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hidden {
  display: none;
}

.container__title {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 20px;
  text-align: center;
}
.container__result {
  font-weight: 700;
}
.container__input {
  display: block;
  margin-bottom: 30px;
  width: 160px;
  height: 40px;
  border: none;
  border-bottom: 1px solid rgb(69, 69, 69);
  outline: none;
  margin-left: auto;
  margin-right: auto;
  // padding-left: 10px;
  text-align: center;
  &:focus {
    outline: none;
  }
}
.container__button {
  display: block;
  background-color: transparent;
  border: 1px solid rgb(69, 69, 69);
  padding: 10px 20px;
  border-radius: 5px;
  transition: 0.3s;
  margin-left: auto;
  margin-right: auto;
  width: 160px;
  &:hover {
    background-color: rgb(227, 227, 227);
  }
}
